//
// SIDEBAR MENU
//

.sidebar {
  float: left;
  position: fixed;
  width: 70px;
  height: 100vh;
  padding: 10px;
  padding-top: $top-height;
  background-color: #fff;
  @include shadow();
  &__menu {
    margin-top: 5px; }
  &__menu-link {
    display: block;
    margin-bottom: 20px;
    @include hide-text();
    @include size(50px);
    @include round(3px);
    &:hover {
      background-color: $background-color; } } }
